import axios from "axios";
import { VueCookieNext } from "vue-cookie-next";

axios.defaults.xsrfCookieName = "csrftoken";
axios.defaults.xsrfHeaderName = "X-CSRFToken";
axios.defaults.baseURL = "https://hwesg2.hyungis.com/api/v1";

const headers = () => {
	const accesstoken = {
		headers: {
			Authorization: `Bearer ${VueCookieNext.getCookie("accesstoken")}`,
		},
	};
	return accesstoken;
};

const deleteData = (data) => {
	delete data["inEdit"];
	delete data["isNew"];
	return data;
};

const editactiveMaster = (data) => {
	data = {
		master_code: data.master_code,
		workplace_name: data.workplace_name,
		workplace_id: data.workplace_id,
		interface_sys: data.interface_sys,
		code_index: data.code_index,
		number: data.number,
		department: data.department,
		typemove: data.typemove,
		costcenter: data.costcenter,
		costcenter_2: data.costcenter_2,
		dataparc_type: data.dataparc_type,
		dataparc_tag: data.dataparc_tag,
	};
	return data;
};

const insertactiveMaster = (data) => {
	var makeMasterCode = "";
	if (data.workplace_name.includes("")) {
		makeMasterCode = "U";
	} else if (data.workplace_name.includes("")) {
		makeMasterCode = "Y";
	} else if (data.workplace_name.includes("2")) {
		makeMasterCode = "S";
	} else {
		makeMasterCode = "T";
	}
	makeMasterCode = makeMasterCode + data.interface_sys + data.code_index;
	data = {
		master_code: makeMasterCode,
		workplace_name: data.workplace_name,
		workplace_id: data.workplace_id,
		interface_sys: data.interface_sys,
		code_index: data.code_index,
		number: data.number,
		department: checkunfined(data.department),
		typemove: checkunfined(data.typemove),
		costcenter: checkunfined(data.costcenter),
		costcenter_2: checkunfined(data.costcenter_2),
		dataparc_type: checkunfined(data.dataparc_type),
		dataparc_tag: checkunfined(data.dataparc_tag),
	};
	return data;
};

const checkunfined = (check) => {
	if (check === undefined) return "";
};

