<template>
	<div class="searchbarWrap">
		<div class="searchWrap Codebar">
			<k-form @submit="handleSubmit">
				<search-form
					:reset="true"
					@clearSearchForm="clearSearchForm"
					@searchFormStateA="searchFormStateA"
				>
					<template v-slot:formdata>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span></span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'group'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<dropdownlist
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="year"
													:value="year_value"
													@change="dropdownChange2"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span></span></label>
								<div class="lists">
									<div class="selectBox">
										<field :name="'group'" :component="'yearSelect'">
											<template v-slot:yearSelect="{ props }">
												<dropdownlist
													v-bind="props"
													:style="{ width: 'auto' }"
													:data-items="year"
													:value="group_valueall"
													@change="dropdownChange2"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
						<div class="shortBox mr-2">
							<div class="wrap">
								<label for=""><span></span></label>
								<div class="lists">
									<div class="selectBox mr-10">
										<field
											:name="'group'"
											:style="{ maxWidth: '230px' }"
											:component="'codeGroupSelect1'"
										>
											<template v-slot:codeGroupSelect1="{ props }">
												<dropdownlist
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="group1"
													:value="group_value1"
													@change="dropdownChange3"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field :name="'group'" :component="'codeGroupSelect'">
											<template v-slot:codeGroupSelect="{ props }">
												<dropdownlist
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="group2"
													:value="group_value2"
													@change="dropdownChange4"
												/>
											</template>
										</field>
									</div>
									<div class="selectBox mr-10">
										<field :name="'group'" :component="'codeGroupSelect'">
											<template v-slot:codeGroupSelect="{ props }">
												<dropdownlist
													v-bind="props"
													:style="{ width: '140px' }"
													:data-items="group3"
													:value="group_value3"
													@change="dropdownChange5"
												/>
											</template>
										</field>
									</div>
								</div>
							</div>
						</div>
					</template>
				</search-form>
			</k-form>
		</div>
	</div>
	<div
		class="p-10t"
		:class="{
			active: this.searchFormStates,
			change: this.$store.state.resize,
		}"
	>
		<div class="p-10 m-0a">
			<div class="grid-code">
				<the-table
					v-if="this.newMngmnDummyData.length !== 0"
					:type="''"
					ref="TheTable"
					:dropdownparameterInput="$store.state.parameterInputCode"
					title=" "
					:addButtonText2="' '"
					:addButtonText3="' '"
					:popupButtonText="' '"
					:addButtonText4="''"
					:dataItems="this.newMngmnDummyData"
					:dropdownData="this.$store.state.dropdownCodeGroup"
					:columns="code_columns"
					:newDataId="newDataId"
					actionName="PARAMETER_GROUP"
					:removeCheck="true"
					@insert="insert"
					@edit="dialogEdit"
					@remove="dialogRemove"
					@isEditableChange="isEditableChange"
				/>
			</div>
		</div>
	</div>
</template>

<script>
import searchicon from "@/assets/img/icon/search.png";
import settingGrid from "../../mixin/TheTable.js";

import { Form, Field } from "@progress/kendo-vue-form";
import SearchForm from "../../components/form/SearchForm.vue";
import { DropDownList } from "@progress/kendo-vue-dropdowns";
import { filterBy } from "@progress/kendo-data-query";
import TheTable from "../../components/grid/TheTable.vue";
import monthData from "../../data/monthDummyData.json";
import newMngmnDummyData from "../../data/extprMngmnDummyData.json";
import activity from "../../data/activityDummyData.json";

export default {
	name: "month",
	beforeMount() {
		this.$store.state.showNavbar = true;
		this.$store.state.showSidebar = true;
		this.$store.state.showTabs = true;
		this.$store.state.showSubNavbar = true;
	},
	components: {
		"k-form": Form,
		field: Field,
		SearchForm,
		dropdownlist: DropDownList,
		TheTable,
	},

	data: function () {
		return {
			group: ["", "", ""],
			group_value: "",
			group_valueall: "",
			type: "",
			newMngmnDummyData,
			activity,
			monthData: monthData,
			year: [2022, 2021, 2020, 2019, 2018, 2017, 2016, 2015, 2014, 2013, 2012],
			year_value: 2022,
			group1: [""],
			group_value1: "",
			group2: [
				"",
				"2",
				"",
				"1",
				"2",
				"3",
				"",
				" ",
				"",
				" ",
				"",
			],
			group_value2: " ",
			group_value3: " ",
			group3: [
				"PE1",
				"PE2",
				"PE3",
				"W&C",
				"CA1",
				"CA2",
				"VCM1",
				"VCM2",
				"ECH",
				"PVC",
				"OA",
				"()",
				"",
				"TDI",
				"PE",
				"VCM",
				"(1)",
				"PVC1",
				"PVC2",
				"(2)",
				"",
				"",
				"",
				"",
				"",
				"",
			],
			group4: [" ", " "],
			group_value4: [""],
			token: this.$cookie.getCookie("accesstoken"),
			searchicon,
			searchData: null,
			visibleDialog: false,
		};
	},

	computed: {
		code_columns() {
			return [
				{
					field: "id",
					title: "",
					width: "100px",
					cell: "dropdownparameterInputCell",
				},
				{
					field: "supportingData",
					title: "",
					cell: "dropdownparameterInputCell",
					width: "150px",
				},

				{
					field: "pjtname1",
					title: "",
					width: "150px",
					cell: "dropdownparameterInputCell",
				},
				{
					field: "pjtstart",
					title: "",
					width: "150px",
					cell: "dropdownparameterInputCell",
				},

				{
					field: "types",
					title: "",
					width: "220px",
				},
				{
					field: "month11",
					title: "",
					width: "150px",
				},
				{
					field: "month1",
					title: "1",
					width: "100px",
				},
				{
					field: "month2",
					title: "2",
					width: "130px",
					resizable: false,
					className: "text-center",
				},
				{
					field: "month3",
					title: "3",
					width: "130px",

					resizable: false,
					className: "text-center",
				},
				{
					field: "month4",
					title: "4",
					width: "130px",

					resizable: false,
					className: "text-center",
				},
				{
					field: "month5",
					title: "5",
					width: "130px",

					resizable: false,
					className: "text-center",
				},
				{
					field: "month5",
					title: "6",
					width: "130px",

					resizable: false,
					className: "text-center",
				},
				{
					field: "month5",
					title: "7",
					width: "130px",

					resizable: false,
					className: "text-center",
				},
				{
					field: "month5",
					title: "8",
					width: "130px",

					resizable: false,
					className: "text-center",
				},
				{
					field: "month5",
					title: "9",
					width: "130px",

					resizable: false,
					className: "text-center",
				},
				{
					field: "month5",
					title: "10",
					width: "130px",

					resizable: false,
					className: "text-center",
				},
				{
					field: "month5",
					title: "11",
					width: "130px",

					resizable: false,
					className: "text-center",
				},
				{
					field: "month5",
					title: "12",
					width: "130px",

					resizable: false,
					className: "text-center",
				},
				{
					field: "files",
					title: "",
					cell: "FileFormCell",
					width: "250px",
				},
				{
					width: "230px",
					cell: "myTemplate",
				},
			];
		},
	},

	methods: {
		setPercentage(percentage) {
			return Math.round(this.gridWidth / 100) * percentage;
		},
		toggleDialog() {
			this.visibleDialog = !this.visibleDialog;
			if (this.visibleDialog == false) {
				this.$refs.TheTable.cancelAll();
			}
		},
		async handleSubmit(dataItem) {
			const searchData = {
				token: this.token,
			};
			if ("code" in dataItem) {
				searchData.code = dataItem.code;
			}
			if ("group" in dataItem) {
				if ("id" in dataItem.group) {
					searchData.group = dataItem.group.id;
				}
			}
			this.searchData = searchData;
			this.fetchGridData();
		},
		fetchGridData() {
			this.$store
				.dispatch("FETCH_CODE_LIST", this.searchData)
				.then(() => this.$refs.TheTable.refreshData())
				.catch((error) => console.log(error));
		},
		dropdownChange1(event) {
			this.group_value = event.value;
		},
		dropdownChange2(event) {
			this.year_value = event.value;
		},
		dropdownChange3(event) {
			this.group_group3 = event.value;
		},
		dropdownChange4(event) {
			this.group_value2 = event.value;
		},
		dropdownChange5(event) {
			this.group_value3 = event.value;
		},
		autoChange(event) {
			this.code_value = event.target.value;
			// this.searchCode = this.filterData(event.target.value);
		},
		filterData(value) {
			const data = this.searchCode;
			const filter = {
				value: value,
				operator: "startswith",
				ignoreCase: true,
			};
			return value ? filterBy(data, filter) : data;
		},
		clearSearchForm() {
			this.searchData = {
				token: this.token,
			};
			this.code_value = "";
			this.code_group_value = {
				text: "",
			};
			this.fetchGridData();
		},
	},
	mixins: [settingGrid()],
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_index.scss";

.pl-6 {
	display: none !important;
}

.subNavigator {
	span {
		font: normal normal normal 12px/18px AppleSDGothicNeoM00;

		&.active {
			color: #33343c;
			font: normal normal normal 12px/18px AppleSDGothicNeoSB00;
		}
	}
}

.Codebar {
	padding: 16px 20px 5px 20px;
	// height: 100px;

	&.active {
		padding: 5px 20px 5px 20px;
		border-radius: none;
		height: auto;
	}

	.longBox {
		// width: 630px !important;
	}

	.selectBox {
		width: auto;
	}

	.buttonBoxs {
		width: 200px;
	}

	.searchInputBox {
		position: relative;

		img {
			position: absolute;
			top: 13px;
			right: 20px;
			height: 14px;
		}
	}
}
</style>
